html {
  font-family: "Myriad Pro";
}
header {
  height: 100px;
}
.head-inner {
  height: 100px;
  width: 600px;
  margin: 0 auto;
}
.head-inner__logo {
  float: left;
}
.head-inner__text {
  display: inline-block;
  line-height: 100px;
  height: 100px;
  font-size: 2em;
  color: #6c6c6c;
}
.head-inner__navi {
  float: right;
  width: 300px;
  height: 100%;
  font-size: 1em;
  color: #6c6c6c;
}
.head-inner__cols {
  vertical-align: middle;
  text-align: center;
}
.billboard {
  width: 100vw;
  min-width: 600px;
  height: 450px;
  background: #eee url("../img/bellaPNG.jpg");
  background-size: 100% 100%;
}
.billboard__text {
  position: relative;
  top: 150px;
  margin: 0 auto;
  width: 600px;
  text-align: center;
  font-size: 2.5em;
  color: #646262;
}
.billboard__button {
  position: relative;
  top: 200px;
  margin: 0 auto;
  width: 250px;
  height: 50px;
  text-align: center;
  font-family: "Myriad Pro", Regular;
  font-size: 1.2em;
  letter-spacing: 1px;
  border-radius: 30px;
  color: #fff;
  background: #ec5483;
  border: 1px solid #f00;
}
.billboard span {
  line-height: 50px;
}
